export async function getBlogPosts(language: string = 'th'): Promise<BlogPost[]> {
  try {
    // Use REST API with language filtering
    const baseUrl = process.env.NEXT_PUBLIC_WORDPRESS_GRAPHQL_URL?.replace('/graphql', '') || 'http://localhost:8006';
    const url = `${baseUrl}/wp-json/sakwood/v1/posts?language=${language}&per_page=10`;

    const response = await fetch(url);
    if (!response.ok) {
      console.error('Failed to fetch blog posts:', response.status);
      // Fall back to GraphQL
      return getBlogPostsViaGraphQL(language);
    }

    const data = await response.json();

    // Handle empty array
    if (!Array.isArray(data) || data.length === 0) {
      console.log(`No blog posts found for language: ${language}`);
      return [];
    }

    // Transform featured image URLs for all posts
    return data.map((post: any) => ({
      ...post,
      featuredImage: transformBlogImage(post.featuredImage),
    }));
  } catch (error) {
    console.error('Failed to fetch blog posts via REST API:', error);
    // Fall back to GraphQL
    return getBlogPostsViaGraphQL(language);
  }
}

/**
 * Fallback: Get blog posts via GraphQL (no language filtering)
 */
async function getBlogPostsViaGraphQL(language: string = 'th'): Promise<BlogPost[]> {
  try {
    const data = await graphqlRequest<BlogPostsResponse>(GET_BLOG_POSTS_QUERY, {
      first: 10,
    });
    const posts = data?.posts?.nodes || [];

    // Filter by language client-side
    const filteredPosts = posts.filter(post => post.language === language);

    // Transform featured image URLs for all posts
    return filteredPosts.map(post => ({
      ...post,
      featuredImage: transformBlogImage(post.featuredImage),
    }));
  } catch (error) {
    console.error('Failed to fetch blog posts via GraphQL:', error);
    return FALLBACK_BLOG_POSTS;
  }
}

export async function getBlogPostBySlug(slug: string, language: string = 'en'): Promise<BlogPost | null> {
  try {
    // Try REST API first with language filtering
    const baseUrl = process.env.NEXT_PUBLIC_WORDPRESS_GRAPHQL_URL?.replace('/graphql', '') || 'http://localhost:8006';
    const restUrl = `${baseUrl}/wp-json/sakwood/v1/posts/${encodeURIComponent(slug)}?language=${language}`;

    const response = await fetch(restUrl);
    if (response.ok) {
      const post = await response.json();
      if (post && !post.code) { // Check if it's not an error response
        return {
          ...post,
          featuredImage: transformBlogImage(post.featuredImage),
        };
      }
    }

    // Fallback to GraphQL if REST API fails
    console.log(`REST API failed for slug: ${slug}, trying GraphQL...`);
    const data = await graphqlRequest<BlogPostResponse>(GET_BLOG_POST_QUERY, { slug });
    const post = data?.post;

    if (post) {
      return {
        ...post,
        featuredImage: transformBlogImage(post.featuredImage),
      };
    }

    // If both fail, return null
    console.warn(`Blog post not found for slug: ${slug} via both REST and GraphQL.`);
    return null;
  } catch (error) {
    console.error('Failed to fetch blog post:', error);
    return null;
  }
}
